
#app2 .tab-bar{
    display: flex;
}
#app2 .tab-bar > li {
    border: 1px solid salmon;
    width: 50%;
}
#app2 .tab-bar > li.selected {
    background: #7AF1FD;
    color: black;
}

#app2 .tab-content > li{
    display: none;
}
#app2 .tab-content > li.active{
    display: block;
}